<template>
<div>
      <Navbar :user-infos="getuser.username"/>
          <h1 class="subheading grey--text" >{{client.toUpperCase()}} SERVICES </h1>
         
      <v-container>
      <v-row >
        <div v-for="service in services" :key="service.service_name">
        <v-col >
      <v-card
    class="mx-auto pa-2"
    max-width="300"
    outlined
  >
  <v-list-item class="d-flex">
         <v-list-item-content class="font-weight-black" style="color:#634034">{{(service.service_name).toUpperCase()}} </v-list-item-content>
          
  </v-list-item>
  <div v-for="i in service.image" :key="i.name">

   <v-card
    class="mx-auto mb-2"
    max-width="270"
    outlined
  >
    <v-list-item three-line>
      <v-list-item-content>
        <div class="text-overline mb-4 d-flex justify-space-between">
          {{client.concat('_'+i.name)}}
          <div>
           <v-avatar
           :color="color[(client.concat('_'+i.name)).slice(0,-4)]"
            size="10"
            class="image-clignote mb-1"
           ></v-avatar>
           {{stat[(client.concat('_'+i.name)).slice(0,-4)]}}
         </div>
        </div>
         
        <v-list-item-title class="text-h5 mb-1">
          {{(i.name).toUpperCase()}}
        </v-list-item-title>
        <v-list-item-subtitle>RAM </v-list-item-subtitle>
        <v-list-item-subtitle >CPU {{checkCpu[(client.concat('_'+i.name)).slice(0,-4)]}}</v-list-item-subtitle>

      </v-list-item-content>

    </v-list-item>

  </v-card>
  </div>

    <v-card-actions>
       <v-btn
      class="ma-2"
      color="info"
      v-model="restart[service.service_name]"
      @click.prevent="getRestart(service.service_name,service.service_version)"
    >
      Restart
      <template v-slot:loader>
        <span class="custom-loader">
          <v-icon light>mdi-cached</v-icon>
        </span>
      </template>
    </v-btn>
      <v-btn
      class="ma-2"
       depressed
      color="error"
      v-model="stop[service.service_name]"
      @click.prevent="getStop(service.service_name,service.service_version)"
    >
      Stop
      <template v-slot:loader>
        <span class="custom-loader">
          <v-icon light>mdi-cached</v-icon>
        </span>
      </template>
    </v-btn>
    <v-btn class="success" v-model="backUp[service.service_name]" @click.prevent="getBackUp(service.service_name,service.service_version)">BackUp</v-btn>
    </v-card-actions>
  </v-card>
  </v-col>
  </div>
      </v-row>
      </v-container>
      <Footer/>   
</div>
</template>
<script>
import Navbar from '@/components/Navbar'
import Footer from '@/components/Footer'
import { mapGetters } from "vuex";
//import axios from 'axios';
//import ClientSum from '../components/ClientSum'
import axios from 'axios'
export default {
   name: 'ClientSrevices',
   data : () => ({
     client : '',
     container: [],
     container_number: '',
     status: [],
     checkCpu: [],
     restart: [],
     stop: [],
     backUp: [],
     stat:[],
     cpu:'',
    info: '',
     services: [],
      color:[],
         
       loader: null,
       loading2: false,
      loading4: false,
      loading: []
  }),
    components: {
    Navbar,
    Footer,
    //ClientSum
  },
  methods:{
    getRestart: function(service,version){
         console.log(service,version)
    },
    getStop: function(service,version){
        console.log(service,version)
    },
    getBackUp: function(service,version){
         console.log(service,version)
    },
      getDocker: function(){
        var data = new FormData()
        var n = 0
        var st = []
        var cp = []
            for (let index = 0; index < this.services.length; index++) {
              for (let i = 0; i < this.services[index].image.length; i++) {
                data.append('containers[]',this.client.concat('_'+this.services[index].image[i].name))
                n++
              }
              
            }
            this.container_number = n
            axios.post('https://saasautomation.dzshops.net/php/echo.php',data)
            .then(rep=>{
             for (let x = 0; x < rep.data.length; x++) {
               if (rep.data[x] !== undefined) {
                     // this.status[0]
                      st[rep.data[x].name]= JSON.parse(rep.data[x].info).State.Status
                     cp[rep.data[x].name] = JSON.parse(rep.data[x].info).HostConfig.CpuPercent
               // tab[0] =JSON.parse(rep.data[0].info) 
                //this.info = tab
                this.status = st
                this.checkCpu = cp
               }

                
             }
          console.log(this.status)
              //console.log(this.container)
              //console.log(this.checkCpu.benaissa_php)
              //console.log(this.status)
              //console.log(rep.data[0].info)
              //console.log(n)
             
              //console.log(rep.data.HostConfig)
              //this.state = rep.data.State.Status
              //this.cpu = rep.data.HostConfig.CpuPercent

             // this.time = rep.data
            })
           
  
       
      },
       
  },
 
   computed: {
    ...mapGetters(["getuser"]),
    
  },
      created(){
        console.log(this.checkCpu['benaissa_php'])
        this.client = this.$route.params.name
       
        var data = new FormData()
        data.append('id',this.$route.params.id)
        axios.post('https://saasautomation.dzshops.net/php/yaml.php',data)  
          .then(res=>{
            var service = []
            var n = 0
            for (let index = 0; index < res.data.length; index++) {
              service[index] = JSON.parse(res.data[index].comp)   
            } 
            this.services = service
             console.log(res.data[0].host)
             console.log(this.services)
             for (let idx = 0; idx < this.services.length; idx++) {
              for (let i = 0; i < this.services[idx].image.length; i++) {
                this.container[n] = this.client.concat('_'+this.services[idx].image[i].name)
                this.color[this.container[n].slice(0,-4)] = 'red'
                this.stat[this.container[n].slice(0,-4)] = 'DOWN'
                //this.checkCpu[this.container[n].slice(0,-4)] = 1
                //this.status[this.container[n].slice(0,-4)] = 'not running'
                n++
              }
              
            }
             console.log(this.container)
              console.log(this.checkCpu.benaissa_php)
              console.log(this.status['benaissa_php'])
              
             setInterval(this.getDocker,10000)
             //this.getDocker()
          })},
         
         
   
   watch: {
    
    
      status: {
      handler: function (val,oldVal) {
        var idex = null
        //console.log(val.benaissa_php)
        //console.log(oldVal)
        for (let s = 0; s < this.container.length; s++) {
          if(val[(this.container[s]).slice(0,-4)] != undefined){
              console.log(val[(this.container[s]).slice(0,-4)])
          if (val[(this.container[s]).slice(0,-4)] != oldVal[this.container[s].slice(0,-4)]) {
             idex = (this.container[s]).slice(0,-4)
             console.log(idex)
          }
          }
          
          
        }
        if (val[idex] == 'running') {
          this.color[idex] = 'green'
          this.stat[idex] = 'UP'
        }
        else{
          this.color[idex] = 'red'
          this.stat[idex] = 'DOWN'
        }
      },
      deep: true
    }
  
    
     

   }
  
}
</script>
<style scoped>
.image-clignote  {
 
   animation-duration: .8s;
   animation-name: clignoter;
   animation-iteration-count: infinite;
   transition: none;
}
@keyframes clignoter {
  0%   { opacity:1; }
  40%   {opacity:0; }
  100% { opacity:1; }
}
.square{
  width: 400px;
  height: 400px;
}
.custom-loader {
    animation: loader 1s infinite;
    display: flex;
  }
  @-moz-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @-webkit-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @-o-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
</style>
